<template>
  <div>
    <el-table
      :data="tabData"
      style="width: 100%;margin-top: -10px"
      border
      size="mini"
      :row-class-name="tableRowClassName"
    >
      <!--index,key-->
      <template v-for="(col, index) in tabHeader">
        <!--,-->
        <el-table-column
          v-if="col.prop === 'id' || col.prop === 'scene_id'"
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          fixed
          width="80px"
        >
        </el-table-column>
        <el-table-column
          v-else-if="col.prop === 'scene_name'"
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          width="250px"
        >
        </el-table-column>
        <el-table-column
          v-else-if="col.prop === 'status'"
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
          width="80px"
          fixed="right"
        >
        </el-table-column>
        <!---->
        <el-table-column
          v-else-if="col.type === 'action'"
          :label="col.label"
          :key="index"
          align="center"
          fixed="right"
          width="80px"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="mini"
              @click="lookClick(tabData[scope.$index].id)"
              >
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          v-else
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          width="150px"
          show-overflow-tooltip
        >
        </el-table-column>
      </template>
    </el-table>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="85%"
      id="resultTab"
    >
      <el-card class="box-card">
        <div slot="header" class="clearfix" style="text-align:center;">
          <h1>
            {{ sceneResultDetail.scene_type + '' }}
          </h1>
        </div>
        <el-row :gutter="20">
          <el-col :span="2">
            <span>&nbsp;&nbsp;&nbsp;&nbsp;id:</span>
          </el-col>
          <el-col :span="4">
            <el-input
              v-model="sceneResultDetail.id"
              placeholder="id"
            ></el-input>
          </el-col>
          <el-col :span="2">
            <span>:</span>
          </el-col>
          <el-col :span="6">
            <el-input
              v-model="sceneResultDetail.scene_name"
              placeholder=""
            ></el-input>
          </el-col>
          <el-col :span="2">
            <span>: </span>
          </el-col>
          <el-col :span="4">
            <el-input
              v-model="sceneResultDetail.project_name"
              placeholder=""
            >
            </el-input>
          </el-col>
        </el-row>
        <el-row :gutter="20" style="margin-top: 10px">
          <el-col :span="2">
            <span>: </span>
          </el-col>
          <el-col :span="4">
            <el-input
              v-model="sceneResultDetail.project_version"
              placeholder=""
            >
            </el-input>
          </el-col>
          <el-col :span="2">
            <span>: </span>
          </el-col>
          <el-col :span="4">
            <el-input
              v-model="sceneResultDetail.business_name"
              placeholder=""
            >
            </el-input>
          </el-col>
          <el-col :span="2">
            <span>: </span>
          </el-col>
          <el-col :span="4">
            <el-input
              v-model="sceneResultDetail.scene_type"
              placeholder=""
            >
            </el-input>
          </el-col>
        </el-row>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix" style="text-align:center;">
          <h1>{{ sceneResultDetail.scene_type }}</h1>
        </div>
        <el-table
          :data="resultData"
          border
          style="width: 100%"
          :row-class-name="resultRowClassName"
        >
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-input
                v-model="resultData[scope.$index].project_name"
                placeholder=""
              >
              </el-input>
            </template>
          </el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-input
                v-model="resultData[scope.$index].business_name"
                placeholder=""
              >
              </el-input>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="500px">
            <template slot-scope="scope">
              <el-input
                v-model="resultData[scope.$index].case_value"
                placeholder=""
              >
              </el-input>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="120px">
            <template slot-scope="scope">
              <el-button
                v-if="resultData[scope.$index].status === ''"
                round
                type="success"
                @click="
                  $refs.resultDialog.lookClick(
                    resultData[scope.$index].case_result_id
                  )
                "
              >
                {{ resultData[scope.$index].status }}
              </el-button>
              <el-button
                v-else-if="resultData[scope.$index].status === ''"
                round
                type="info"
                @click="
                  $refs.resultDialog.lookClick(
                    resultData[scope.$index].case_result_id
                  )
                "
              >
                {{ resultData[scope.$index].status }}
              </el-button>
              <el-button
                v-else-if="resultData[scope.$index].status === ''"
                round
              >
                {{ resultData[scope.$index].status }}
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="dialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <case-result-dialog ref="resultDialog"> </case-result-dialog>
  </div>
</template>

<script>
import CaseResultDialog from '../CaseTemplate/CaseResultDialog';

export default {
  // 
  name: 'SceneResultTable',
  components: { caseResultDialog: CaseResultDialog },

  // (tabData:;tabHeader:;tabHeight:,)
  props: {
    tabData: null,
    tabHeader: null,
    tabHeight: null
  },
  data() {
    return {
      dialogVisible: false,
      sceneResultDetail: {},
      resultData: []
    };
  },
  methods: {
    // tab
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'warning-row';
      } else {
        return 'success-row';
      }
    },

    // 
    resultRowClassName({ row }) {
      if (row.status === '') {
        return 'error-row';
      } else if (row.status === '') {
        return 'warn-row';
      } else {
        return 'normal-row';
      }
    },
    lookClick(id) {
      this.dialogVisible = true;
      let editUrl = String.format(
        '{0}/sceneInterface/sceneResultInfo?id={1}',
        this.GLOBAL.MONITOR_URL,
        id
      );
      this.axios
        .get(editUrl)
        .then(response => {
          if (response.data.code !== '200') {
            this.$message.error(response.data.msg);
          }
          this.sceneResultDetail = response.data.sceneResultInfo;
          this.resultData = response.data.sceneResultInfo.case_info_list;
          return response;
        })
        .catch(response => {
          return response;
        });
    }
  }
};
</script>
<style scoped>
.el-table >>> .warning-row {
  background: oldlace;
}

.el-table >>> .success-row {
  background: #f0f9eb;
}

.el-table >>> .error-row {
  background: orangered;
}

.el-table >>> .warn-row {
  background: moccasin;
}

.el-table >>> .normal-row {
  background: white;
}

/deep/ #resultTab .el-table tbody tr:hover td {
  background-color: skyblue !important;
}

.el-row {
  line-height: 40px;
  margin-top: 0px;
}
</style>



